/* Edit Teacher Card Section
--------------------------------------------------------------------------------------*/
#teacher_wrap {
    .teacher {
        background-color: $cardsbg;
    }
    .teacher:hover {
        background-color: hsla(0, 0%, 0%, 0.9);

        button {
         //   background-color: hsla(0, 0%, 100%, 0.5);
         background-color: $footerbg;
        }
    }

    figure {
        background-color: initial;
    }

    figure:hover {
        outline: none;
    }
}

.teacher:hover {     
    .edit-option {
        display: flex;
    }
}

.teacher-caption {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto;

    figcaption {
        grid-column: 1/1;
        grid-row: 1/1;
    }

    .edit-option {
        grid-column: 1/1;
        grid-row: 1/1;

        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin-top: 5em;
        display: none;

        button {
            width: 130px;
            height: 20px;
            border: none;
            padding-left: 0;
            padding-right: 0;
            font-size: 1em;
            
            overflow: hidden;

            position: relative;
            z-index: 1;
            background-color: hsla(0, 0%, 100%, 0.5);

            a {
                display: flex;
                justify-content: center;
                color: #fff;
                text-decoration: none;
                text-align: center;
            }
        }

        button::before {
            content: "";
            width: 100%;
            height: 30px;
            position: absolute;
            z-index: -1;
            top: 0;
            left: 0;
            background-color: hsla(0, 0%, 0%, 0.3);
    
            transform: translate3d(0px, -30px, 0px);
            transform-origin: top center;
    
            transition-property: transform;
            transition-duration: 0.3s;
            transition-delay: 0s;
            transition-timing-function: ease-out;
        }
    
        button:hover,
        button:focus {
            color: #fff;
        }
    
        button:hover:before,
        button:focus:before {
            transform: translate3d(0px, 0px, 0px);
        }

        button:nth-of-type(1) {
            margin-right: 10px;
        }
    }
}

#edit-section {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    right: 0;
    height: 0;
    z-index: 1;
    opacity: 0;
    background: #fff;
    visibility: hidden;
    transition: 0.6s;
}

#edit-section:target {
    height: 250vh;
    opacity: 1;
    visibility: visible;

    form {
        position: relative;
    }

    fieldset {
        display: grid;
        grid-template-columns: 30em;
        grid-template-rows: repeat(4, auto);
        justify-content: center;
        grid-row-gap: 2em;
        background-color: lightgray;

        hgroup {
            grid-column: 1/2;
            grid-row: 1/2;

            h3 {
                text-align: center;
            }
        }

        span:nth-of-type(1) {
            grid-column: 1/2;
            grid-row: 2/3;

            perspective: 300rem;

            position: relative;
    
            input {
                border: none;
                outline: none;
                border-bottom: 1px solid #000;
                background-color: transparent;
            }
    
            label {
                position: absolute;
                left: 0;
                top: 0;
                font-size: 1em;
    
                transform-origin: bottom;
                transform: none;
    
                transition-property: transform, font-size;
                transition-duration: 0.5s, 0.3s;
                transition-delay: 0s, 0s;
                transition-timing-function: ease-in-out;
            }
    
            input:focus + label,
            input:valid + label {
                transform: translate3d(0px, -20px, 0px);
                font-size: 0.8em;
                font-weight: none;
            }
        }

        span:nth-of-type(2) {
            grid-column: 1/2;
            grid-row: 3/4;
        }

        span:nth-of-type(3) {
            grid-column: 1/2;
            grid-row: 4/5;

            textarea {
                width: 25em;
                height: 15em;
                margin-left: 5em;
            }
        }

        span:nth-of-type(4) {
            grid-column: 1/2;
            grid-row: 5/6;
            margin: 0 auto;

            button {
                width: 150px;
                padding: 5px;
                border: none;
                font-size: 1em;
                
                overflow: hidden;
    
                position: relative;
                z-index: 1;
                background-color: hsla(0, 0%, 0%, 0.5);
            }
    
            button::before {
                content: "";
                width: 100%;
                height: 30px;
                position: absolute;
                z-index: -1;
                top: 0;
                left: 0;
                background-color: hsla(0, 0%, 0%, 0.3);
        
                transform: translate3d(0px, -30px, 0px);
                transform-origin: top center;
        
                transition-property: transform;
                transition-duration: 0.3s;
                transition-delay: 0s;
                transition-timing-function: ease-out;
            }
        
            button:hover,
            button:focus {
                color: #fff;
                cursor: pointer;
            }
        
            button:hover:before,
            button:focus:before {
                transform: translate3d(0px, 0px, 0px);
            }
        }
    }
}

.close {
    position: absolute;
    right: 1.5rem;
    top: 0.5rem;
    font-size: 2rem;
    font-weight: 900;
    text-decoration: none;
    color: inherit;
}

/* Add Teacher Card Section
--------------------------------------------------------------------------------------*/
.new-teacher {
    img {
        display: block;
        width: 100%;
        height: 213.328px;
        margin: 0;
    }
    
    div {
        height: 34.3em;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: flex-start;
        margin-top: 3.5em;

        button {
            width: 130px;
            border: none;
            padding-left: 0;
            padding-right: 0;
            font-size: 1em;
            
            overflow: hidden;

            position: relative;
            z-index: 1;
            background-color: hsla(0, 0%, 0%, 0.9);

            a {
                display: flex;
                justify-content: center;
                padding: 1px;
                color: #fff;
                text-decoration: none;
                text-align: center;
            }
        }

        button::before {
            content: "";
            width: 100%;
            height: 30px;
            position: absolute;
            z-index: -1;
            top: 0;
            left: 0;
            background-color: hsla(0, 0%, 0%, 0.3);
    
            transform: translate3d(0px, -30px, 0px);
            transform-origin: top center;
    
            transition-property: transform;
            transition-duration: 0.2s;
            transition-delay: 0s;
            transition-timing-function: ease-out;
        }
    
        button:hover,
        button:focus {
            color: #fff;
        }
    
        button:hover:before,
        button:focus:before {
            transform: translate3d(0px, 0px, 0px);
        }
    }
}

