/* Feedback Card Section
--------------------------------------------------------------------------------------*/
.feedback-card {
    height: 50em;

    .student-feedback {
        margin-top: 10em;
    }

    .figcaption-card {
        height: 200px;
        overflow: hidden;
        font-size: $comment-card-font-size;
        transition-property: height;
        transition-duration: 0.5s;
        transition-delay: 0s;
        transition-timing-function: ease-in-out;
        background-color: $cardsbg;
        border-radius: 5%;

        h3 {
            margin: 0;
            padding-top: 2em;
            text-align: center;
        }

        .hgroup-card {
            margin-top: 1em;
        }
        .hgroup-card p {
            margin: 0;
        }

        p {
            padding-left: 1em;
            padding-right: 0.5em;
        }
    }
  
    button {
        display: block;
        width: 150px;
        margin: 0.5em auto;
        padding: 5px;
        border: none;
        font-size: 1em;
        
        overflow: hidden;

        position: relative;
        z-index: 1;
     //   background-color: hsla(0, 0%, 0%, 0.5);
        background-color: $footerbg;
    }

    button::before {
        content: "";
        width: 100%;
        height: 30px;
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        background-color: $headerbg;
   //     background-color: hsla(0, 0%, 0%, 0.3);

        transform: translate3d(0px, -30px, 0px);
        transform-origin: top center;

        transition-property: transform;
        transition-duration: 0.3s;
        transition-delay: 0s;
        transition-timing-function: ease-out;
    }

    button:hover {
        color: #fff;
        cursor: pointer;
    }

    button:hover:before {
        transform: translate3d(0px, 0px, 0px);
    }
}

.comment {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto;
    justify-content: center;

    div:nth-of-type(1) {
        grid-column: 1/2;
        grid-row: 1/2;
    }

    div:nth-of-type(2) {
        grid-column: 1/2;
        grid-row: 1/2;
        
        display: flex;
        flex-wrap: wrap;

        display: none;

        button {
            width: 130px;
            height: 25px;
            border: none;
            margin: 5em auto;
            padding-left: 0;
            padding-right: 0;
            font-size: 1em;

            overflow: hidden;

            position: relative;
            z-index: 1;
            background-color: hsla(0, 0%, 100%, 0.5);

            a {
                display: flex;
                justify-content: center;
                color: #fff;
                text-decoration: none;
                text-align: center;
            }
        }

        button::before {
            content: "";
            width: 100%;
            height: 30px;
            position: absolute;
            z-index: -1;
            top: 0;
            left: 0;
            background-color: hsla(0, 0%, 0%, 0.3);
    
            transform: translate3d(0px, -30px, 0px);
            transform-origin: top center;
    
            transition-property: transform;
            transition-duration: 0.3s;
            transition-delay: 0s;
            transition-timing-function: ease-out;
        }
    
        button:hover,
        button:focus {
            color: #fff;
        }
    
        button:hover:before,
        button:focus:before {
            transform: translate3d(0px, 0px, 0px);
        }

        button:nth-of-type(1) {
            margin-right: 10px;
        }
    }

    &:hover {
        background-color: hsla(0, 0%, 0%, 0.8);

        div:nth-of-type(2) {
            display: flex;
        }
    }
}

/* Media Query
--------------------------------------------------------------------------------------*/
@include breakpoints(min-width, 18em) {
    .feedback-card {
        display: grid;
        grid-template-columns: 17em;
        grid-template-rows: repeat(4, auto);
        grid-gap: 2em;
        justify-content: center;

        div[class^="student"]:nth-of-type(1) {
            grid-column: 1/2;
            grid-row: 1/2;
        }

        div[class^="student"]:nth-of-type(2) {
            grid-column: 1/2;
            grid-row: 2/3;
        }

        div[class^="student"]:nth-of-type(3) {
            grid-column: 1/2;
            grid-row: 3/4;
        }

        div[class^="student"]:nth-of-type(4) {
            grid-column: 1/2;
            grid-row: 4/5;
        }
    } 
}

@include breakpoints(min-width, 20em) {
    .feedback-card {
        display: grid;
        grid-template-columns: minmax(17em, 18em);
        grid-template-rows: repeat(4, auto);
        grid-gap: 2em;
        justify-content: center;

        div[class^="student"]:nth-of-type(1) {
            grid-column: 1/2;
            grid-row: 1/2;
        }

        div[class^="student"]:nth-of-type(2) {
            grid-column: 1/2;
            grid-row: 2/3;
        }

        div[class^="student"]:nth-of-type(3) {
            grid-column: 1/2;
            grid-row: 3/4;
        }

        div[class^="student"]:nth-of-type(4) {
            grid-column: 1/2;
            grid-row: 4/5;
        }
    } 
}

@include breakpoints(min-width, 40em) {
    .feedback-card {
        display: grid;
        grid-template-columns: repeat(2, minmax(17em, 18em));
        grid-template-rows: repeat(4, auto);
        grid-gap: 2em;
        justify-content: center;

        div[class^="student"]:nth-of-type(1) {
            grid-column: 1/2;
            grid-row: 1/2;
        }

        div[class^="student"]:nth-of-type(2) {
            grid-column: 2/3;
            grid-row: 1/2;
        }

        div[class^="student"]:nth-of-type(3) {
            grid-column: 1/2;
            grid-row: 2/3;
        }

        div[class^="student"]:nth-of-type(4) {
            grid-column: 2/3;
            grid-row: 3/4;
        }
    } 
}

@include breakpoints(min-width, 70em) {
    .feedback-card {
        display: grid;
        grid-template-columns: repeat(4, minmax(18em, 20em));
        grid-template-rows: repeat(4, auto);
        grid-gap: 2em;
        justify-content: center;

        div[class^="student"]:nth-of-type(1) {
            grid-column: 1/2;
            grid-row: 1/2;
        }

        div[class^="student"]:nth-of-type(2) {
            grid-column: 2/3;
            grid-row: 1/2;
        }

        div[class^="student"]:nth-of-type(3) {
            grid-column: 3/4;
            grid-row: 1/2;
        }

        div[class^="student"]:nth-of-type(4) {
            grid-column: 4/5;
            grid-row: 1/2;
        }
    } 
}