
/* Teacher Section
--------------------------------------------------------------------------------------*/
#teacher_wrap {
   // background-color: gray;

    figure {
        height: 345px;
        overflow: hidden;
        margin: 0;
        padding: 1em;
        background-color: $cardsbg;
        border-radius: 2% 2% 5% 5%;
        transition: all 0.5s ease-in-out;
    }

    figure:hover {
        cursor: pointer;
    }
    
    .teacher {
        transition-property: all;
        transition-duration: 0.5s;
        transition-delay: 0s;
        transition-timing-function: ease;
        border-radius: 2% 2% 5% 5%;

        h4 {
            text-align: center;
        }
    }
    
    .teacher:hover {
        margin: -5px 0px 0px -5px;
        cursor: pointer;
        box-shadow: 10px 10px 10px #000;
        background-color: $cardsbg;
    }
    
    img{
        display: block;
        width: 100%;
        margin: 0;
    }

    figcaption {
        margin: 0;
        padding-top: 1em;
        padding-left: 10px;
    }
}

.BTN-contianer {
    button {
        display: block;
        width: 150px;
        margin: 0.5em auto;
        padding: 5px;
        border: none;
        font-size: 1em;
        
        overflow: hidden;

        position: relative;
        z-index: 1;
 //       background-color: hsla(0, 0%, 0%, 0.5);
        background-color: $footerbg;
    }

    button::before {
        content: "";
        width: 100%;
        height: 30px;
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        background-color: $headerbg;
//        background-color: hsla(0, 0%, 0%, 0.3);

        transform: translate3d(0px, -30px, 0px);
        transform-origin: top center;

        transition-property: transform;
        transition-duration: 0.3s;
        transition-delay: 0s;
        transition-timing-function: ease-out;
    }

    button:hover {
        color: #fff;
        cursor: pointer;
    }

    button:hover:before {
        transform: translate3d(0px, 0px, 0px);
    }
}

/*  Media Query Section
--------------------------------------------------------------------------------------*/
@include breakpoints(min-width, 20em) {
    #teacher_wrap {
        #teacher-card {
            display: grid;
            grid-template-columns: minmax(18em, 19em);
            grid-template-rows: repeat(15, auto);
            grid-gap: 1em;

            padding-top: 2em;
            padding-bottom: 2em;
            justify-content: center;
    
            div:nth-of-type(1) {
                grid-column: 1/2;
                grid-row: 1/2;
                width: 100%;
            }
            
            div:nth-of-type(2) {
                grid-column: 1/2;
                grid-row: 2/3;
            }
    
